@font-face {
  font-family: "Oswald Medium";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Oswald Regular";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 320px;
  outline: 1px solid gold;
}
.container {
  margin: 0 auto;
  padding: 0;
  width: 320px;
}
.page-header {
  padding-top: 13px;
  display: flex;
  flex-direction: column;
  width: 320px;
}
.page-header__logo {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  margin-bottom: 14px;
}
.logo {
  width: 191px;
  height: 38px;
}
.logo-image {
  width: 191px;
  height: 38px;
}
.intro {
  margin-bottom: 22px;
  padding-top: 26px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
}
.intro__title {
  margin: 0;
  margin-bottom: 26px;
  padding: 0;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
}
.intro__slogan {
  margin: 0 auto;
  margin-bottom: 28px;
  width: 280px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  word-wrap: break-word;
}
.intro__product-image {
  margin: 0;
  margin-bottom: 4px;
  padding: 0;
}
.button {
  margin: 0;
  padding: 11px;
  display: block;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  word-wrap: break-word;
  word-break: break-all;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: -46px;
  right: 32px;
  width: 24px;
  height: 24px;
  font-size: 0;
  background-color: transparent;
  border: none;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(-45deg);
}
.main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
}
.site-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
}
.site-list__item {
  margin: 0;
  padding: 22px;
  padding-bottom: 20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.site-list__item:last-of-type {
  border-bottom: 1px solid #e6e6e6;
}
.site-list__link {
  margin: 0;
  padding: 0;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 20px;
  font-weight: bold;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  word-wrap: break-word;
}
.site-list__link:hover,
.site-list__link:focus {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
.categories-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}
.categories-item {
  margin-bottom: 20px;
  padding-top: 30px;
  padding-bottom: 22px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #f2f2f2;
}
.categories-item__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  padding-left: 92px;
  position: relative;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: normal;
  line-height: 37px;
  color: #000000;
  text-transform: uppercase;
}
.categories-item__title::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}
.categories-item__title--slim::after {
  left: 22px;
  top: -5px;
}
.categories-item__title--slim::before {
  content: "";
  position: absolute;
  width: 35px;
  height: 49px;
  top: -8px;
  left: 30px;
  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  z-index: 10;
}
.categories-item__title--pro::after {
  top: -6px;
  left: 22px;
}
.categories-item__title--pro::before {
  content: "";
  position: absolute;
  width: 67px;
  height: 28px;
  top: 5px;
  left: 13px;
  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
  z-index: 10;
}
.categories-item__description {
  margin: 0 20px;
  margin-bottom: 19px;
  padding-bottom: 21px;
  width: 240px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}
.categories-item__button {
  margin-left: 20px;
  position: relative;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}
.categories-item__button:active {
  opacity: 0.3;
}
.categories-item__button----slim::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 97px;
  width: 24px;
  height: 11px;
  background-image: url("../img/icon-right-arrow-long.svg");
}
.categories-item__button--pro::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 95px;
  width: 24px;
  height: 11px;
  background-image: url("../img/icon-right-arrow-long.svg");
}
.advantages {
  padding-top: 9px;
}
.advantages__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
}
.advantages__item {
  margin-bottom: 10px;
  padding: 8px;
  padding-right: 0;
  padding-left: 81px;
  position: relative;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  transform: translateY(-30px);
}
.advantages__item:last-of-type {
  margin-bottom: 0;
}
.advantages__item--substance::before {
  background-image: url("../img/advantage-leaf@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.advantages__item--powder::before {
  background-image: url("../img/advantage-powder@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.advantages__item--meal::before {
  background-image: url("../img/advantage-dining@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.advantages__item--period::before {
  background-image: url("../img/advantage-clock@1x.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.advantages__title {
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  transform: none;
  color: #000000;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
}
